<script>
import { showWarningMessage, showSuccessMessage } from '@/utils/message.js';
import { QUERYOPERATOR } from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
import {
  getListSearchHistoryBySearchTypeEnum,
  insertSearchHistory,
  deleteById
} from '@/api/public';
export default {
  name: 'SearchBox',

  props: {
    // 表头字段名，用于为下拉框赋值
    fields: {
      type: Array,
      default: () => [],
      required: false
    },
    searchList: {
      type: Array,
      default: () => [],
      required: false
    },
    types: {
      type: Array,
      default: () => [],
      required: false
    }
  },
  data() {
    return {
      // 展开按钮图标
      searchName: '',
      dialogVisible: false,
      expandBtnIcon: 'search-bar-expand',
      SearchHistoryList: [],
      // 连接类型选项
      typeOptions1: [],
      typeOptions2: [],
      typeOptions3: [],
      typeOptions4: [],
      typeOptions5: [],
      typeOptions6: [],
      // 查询字段
      searchData1: [],
      searchData2: [],
      searchData3: [],
      searchData4: [],
      searchData5: [],
      searchData6: [],

      typeOptions: [],
      // 逻辑运算选项
      operatorOptions: [
        {
          label: '和',
          value: 1
        },
        {
          label: '或',
          value: 2
        }
      ],

      // 大组检逻辑运算下拉框的动态样式
      tagName: 'text—item-hide',
      width80: 'width-80',
      rowStartThree: 'row-start-3',
      rowStartOne: 'row-start-1',
      columnExpand: 'column-expand',
      columnHide: 'column-hide',
      marginTop10: 'margin-top-10',
      formArea: 'form-area',
      areaA: 'area-a',
      areaB: 'area-b',
      areaC: 'area-c',
      areaD: 'area-d',
      areaE: 'area-e',
      areaF: 'area-f',
      areaG: 'area-g',
      areaH: 'area-h',
      areaI: 'area-i',
      areaJ: 'area-j',
      areaK: 'area-k',
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }
        ]
      },
      // 保存输入的搜索条件
      condition1Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      condition2Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      condition3Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      condition4Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      condition5Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      condition1Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      condition2Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      condition3Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      condition4Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      condition5Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1,
        valueList: []
      },
      // 大组之间的逻辑运算
      groupOperator: 1,
      // 是否显示隐藏的条件输入行
      isHiddenSearchLineShow: false
    };
  },

  computed: {
    // 根据传入的表格表头配置返回下拉选项列表
    fieldOptions: function() {
      const arr = [];
      for (const column of this.fields) {
        if (column.field) {
          arr.push({
            field: column.field,
            title: column.title
          });
        }
      }

      return arr;
    }
  },

  watch: {
    types: {
      handler: function(val) {
        this.typeOptions = [...val];
      },
      immediate: true
    },
    searchList(val) {
      this.getListSearchHistory();
    }
  },
  // mounted() {
  //   this.getListSearchHistory();
  // },
  methods: {
    handleClose() {
      this.dialogVisible = false;
    },
    /**
     * @description:查询联动事件
     * @param {type}
     * @return:
     * @author: CH
     */
    selectCahnge1(event) {
      this.typeOptions1 = [];
      this.searchData1 = [];
      this.condition1Group1.value = '';
      this.condition1Group1.type = '';
      this.condition1Group1.ptype = '';
      this.condition1Group1.valueList = ['', '', ''];
      for (var i in this.searchList) {
        if (this.searchList[i].sqlName === event) {
          this.condition1Group1.ptype = this.searchList[i].parameterType;
          this.searchData1 = this.searchList[i];
          this.typeOptions1 = this.queryoperator(
            this.searchList[i].queryOperator
          );
        }
      }
    },
    selectCahnge2(event) {
      this.typeOptions2 = [];
      this.searchData2 = [];
      this.condition1Group2.value = '';
      this.condition1Group2.type = '';
      this.condition1Group2.ptype = '';
      this.condition1Group2.valueList = ['', '', ''];
      for (var i in this.searchList) {
        if (this.searchList[i].sqlName === event) {
          this.condition1Group2.ptype = this.searchList[i].parameterType;
          this.searchData2 = this.searchList[i];
          this.typeOptions2 = this.queryoperator(
            this.searchList[i].queryOperator
          );
        }
      }
    },
    selectCahnge3(event) {
      this.typeOptions3 = [];
      this.searchData3 = [];
      this.condition2Group1.value = '';
      this.condition2Group1.type = '';
      this.condition2Group1.ptype = '';
      this.condition2Group1.valueList = ['', '', ''];
      for (var i in this.searchList) {
        if (this.searchList[i].sqlName === event) {
          this.condition2Group1.ptype = this.searchList[i].parameterType;
          this.searchData3 = this.searchList[i];
          this.typeOptions3 = this.queryoperator(
            this.searchList[i].queryOperator
          );
        }
      }
    },
    selectCahnge4(event) {
      this.typeOptions4 = [];
      this.searchData4 = [];
      this.condition2Group2.value = '';
      this.condition2Group2.type = '';
      this.condition2Group2.ptype = '';
      this.condition2Group2.valueList = ['', '', ''];
      for (var i in this.searchList) {
        if (this.searchList[i].sqlName === event) {
          this.condition2Group2.ptype = this.searchList[i].parameterType;
          this.searchData4 = this.searchList[i];
          this.typeOptions4 = this.queryoperator(
            this.searchList[i].queryOperator
          );
        }
      }
    },
    selectCahnge5(event) {
      this.typeOptions5 = [];
      this.searchData5 = [];
      this.condition3Group1.value = '';
      this.condition3Group1.type = '';
      this.condition3Group1.ptype = '';
      this.condition3Group1.valueList = ['', '', ''];
      for (var i in this.searchList) {
        if (this.searchList[i].sqlName === event) {
          this.condition3Group1.ptype = this.searchList[i].parameterType;
          this.searchData5 = this.searchList[i];
          this.typeOptions5 = this.queryoperator(
            this.searchList[i].queryOperator
          );
        }
      }
    },
    selectCahnge6(event) {
      this.typeOptions6 = [];
      this.searchData6 = [];
      this.condition3Group2.value = '';
      this.condition3Group2.type = '';
      this.condition3Group2.ptype = '';
      this.condition3Group2.valueList = ['', '', ''];
      for (var i in this.searchList) {
        if (this.searchList[i].sqlName === event) {
          this.condition3Group2.ptype = this.searchList[i].parameterType;
          this.searchData6 = this.searchList[i];
          this.typeOptions6 = this.queryoperator(
            this.searchList[i].queryOperator
          );
        }
      }
    },
    queryoperator(data) {
      var queryData = [];
      for (var key in QUERYOPERATOR) {
        for (var i in data) {
          if (key === data[i]) {
            queryData.push({
              type: QUERYOPERATOR[key],
              value: data[i]
            });
          }
        }
      }
      return queryData;
    },
    /**
     * @description: （搜索框）点击“展开”按钮，触发事件
     * @param 无
     * @return: boolean
     * @author: cyb
     */
    handleExpandEvent() {
      // 展开
      if (!this.isHiddenSearchLineShow) {
        this.isHiddenSearchLineShow = true;
        // 切换图标
        this.expandBtnIcon = 'search-bar-hide';
        this.tagName = 'text—item-show';
        return false;
      }

      // 收起
      if (
        this.isHiddenSearchLineShow &&
        this.expandBtnIcon === 'search-bar-hide'
      ) {
        this.isHiddenSearchLineShow = false;
        this.expandBtnIcon = 'search-bar-expand';
        this.tagName = 'text—item-hide';
      }
    },

    /**
     * @description: （搜索框）判断某组条件是否填写完整
     * @param {object} condition
     * @return: boolean
     * @author: cyb
     */
    checkCondition(condition) {
      if (condition.column === 'quotaType') {
        condition.value = condition.valueList.join(',');
        condition.type = 'eq';
        for (let i = 0; i < 3; i++) {
          if (condition.valueList[i] === '' || condition.valueList[i] === undefined) {
            return false;
          }
        }
      }
      for (const key of Object.keys(condition)) {
        if (!condition[key]) {
          return false;
        }
      }

      return true;
    },

    /**
     * @description: （搜索框）点击“查询”按钮，触发方法
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleSearchEvent() {
      const conditionArr1 = [];
      const conditionArr2 = [];
      for (let index = 1; index < 4; index++) {
        const element = this[`condition${index}Group1`];
        // element.ptype = 'string';
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr1.push(element);
        }
      }

      for (let index = 1; index < 4; index++) {
        const element = this[`condition${index}Group2`];
        // element.ptype = 'string';
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr2.push(element);
        }
      }

      // 如果没有一组条件填写完整，则不做查询
      if (conditionArr1.length === 0 && conditionArr2.length === 0) {
        if (!this.$slots.default) {
          showWarningMessage('查询条件未填写完整！');
          return false;
        } else {
          this.$emit('searchBtnClick', conditionArr1);
        }
      }

      // 如果只填写了第一组的条件，将第一组的条件传给父页面进行查询
      if (conditionArr1.length > 0 && conditionArr2.length === 0) {
        // 提交搜索条件
        this.$emit('searchBtnClick', conditionArr1);
      }

      // 如果只填写了第二组的条件，将第二组的条件传给父页面进行查询
      if (conditionArr2.length > 0 && conditionArr1.length === 0) {
        // 提交搜索条件
        this.$emit('searchBtnClick', conditionArr2);
      }
      // 如果两组条件都有填写，则将两个数组以及数组间的逻辑运算类型都作为参数传给父页面
      if (conditionArr1.length > 0 && conditionArr2.length > 0) {
        // 根据搜索条件查询数据
        this.$emit(
          'searchBtnClick',
          conditionArr1,
          this.groupOperator,
          conditionArr2
        );
      }
    },

    /**
     * @description: （搜索框）清除所有条件输入
     * @param 无
     * @return: void
     * @author: cyb
     */
    resetCondtionGroup() {
      for (let i = 1; i < 3; i++) {
        for (let j = 1; j < 4; j++) {
          this[`condition${j}Group${i}`] = Object.assign(
            {},
            this[`condition${j}Group${i}`],
            {
              column: '',
              type: '',
              value: '',
              operator: 1,
              valueList: []
            }
          );
        }
      }
      for (let s = 1; s < 7; s++) {
        this[`typeOptions${s}`] = [];
        this[`searchData${s}`] = [];
      }

      this.groupOperator = 1;
    },

    /**
     * @description: (搜索框)“重置”按钮点击触发事件
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleResetSearchConditionEvent() {
      this.resetCondtionGroup();

      // 用于重置插槽数据
      this.$emit('searchReset');
      this.$emit('searchBtnClick');
    },
    /**
     * @description: 查询搜索历史
     * @param {type}
     * @return:
     * @author: CH
     */
    async getListSearchHistory() {
      try {
        const res = await getListSearchHistoryBySearchTypeEnum({
          searchTypeEnum: this.searchList[0].searchType
        });
        if (res.code === '0000') {
          // 将请求数据写入table
          this.SearchHistoryList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(error);
      } finally {
      }
    },
    isDeleteListSearchHistory(item) {
      this.$confirm('确认删除？')
        .then(_ => {
          this.deleteListSearchHistory(item);
        })
        .catch(_ => {});
    },
    /**
     * @description: 查询搜索历史
     * @param {type}
     * @return:
     * @author: CH
     */
    async deleteListSearchHistory(item) {
      try {
        const res = await deleteById({
          id: item.searchId
        });
        if (res.code === '0000') {
          this.getListSearchHistory();
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(error);
      } finally {
      }
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: CH
     */
    showDialog() {
      const conditionArr1 = [];
      const conditionArr2 = [];
      for (let index = 1; index < 4; index++) {
        const element = this[`condition${index}Group1`];
        // element.ptype = 'string';
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr1.push(element);
        }
      }

      for (let index = 1; index < 4; index++) {
        const element = this[`condition${index}Group2`];
        // element.ptype = 'string';
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr2.push(element);
        }
      }

      // 如果没有一组条件填写完整，则不做查询
      if (conditionArr1.length === 0 && conditionArr2.length === 0) {
        showWarningMessage('查询条件未填写完整！');
        return false;
      }
      this.dialogVisible = true;
    },
    /**
     * @description: 保持搜索历史
     * @param {type}
     * @return:
     * @author: CH
     */
    async saveListSearchHistory() {
      const conditionArr1 = [];
      const conditionArr2 = [];
      for (let index = 1; index < 4; index++) {
        const element = this[`condition${index}Group1`];
        // element.ptype = 'string';
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr1.push(element);
        }
      }

      for (let index = 1; index < 4; index++) {
        const element = this[`condition${index}Group2`];
        // element.ptype = 'string';
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr2.push(element);
        }
      }

      // 如果没有一组条件填写完整，则不做查询
      if (conditionArr1.length === 0 && conditionArr2.length === 0) {
        showWarningMessage('查询条件未填写完整！');
        return false;
      }

      if (conditionArr1 !== undefined) {
        var newconditionArr1 = JSON.parse(JSON.stringify(conditionArr1));
        for (var s in newconditionArr1) {
          newconditionArr1[s].value = newconditionArr1[s].value.toString();
        }
      }
      if (conditionArr2 !== undefined) {
        var newconditionArr2 = JSON.parse(JSON.stringify(conditionArr2));
        for (var x in newconditionArr2) {
          newconditionArr2[x].value = newconditionArr2[x].value.toString();
        }
      }

      // // 如果只填写了第一组的条件，将第一组的条件传给父页面进行查询
      // if (conditionArr1.length > 0 && conditionArr2.length === 0) {
      //   // 提交搜索条件
      //   this.$emit('searchBtnClick', conditionArr1);
      // }

      // // 如果只填写了第二组的条件，将第二组的条件传给父页面进行查询
      // if (conditionArr2.length > 0 && conditionArr1.length === 0) {
      //   // 提交搜索条件
      //   this.$emit('searchBtnClick', conditionArr2);
      // }
      // // 如果两组条件都有填写，则将两个数组以及数组间的逻辑运算类型都作为参数传给父页面
      // if (conditionArr1.length > 0 && conditionArr2.length > 0) {
      //   // 根据搜索条件查询数据
      //   this.$emit(
      //     'searchBtnClick',
      //     conditionArr1,
      //     this.groupOperator,
      //     conditionArr2
      //   );
      // }
      if (this.searchName === '' || this.searchName.split(' ').join('').length === 0) {
        showWarningMessage('请输入检索历史的名称');
        this.searchName = '';
        return false;
      }
      try {
        const res = await insertSearchHistory(
          {
            searchTypeEnum: this.searchList[0].searchType,
            searchName: this.searchName
          },
          {
            conditionJson1: newconditionArr1,
            operator: this.groupOperator,
            conditionJson2: newconditionArr2
          }
        );
        if (res.code === '0000') {
          // 将请求数据写入table
          this.dialogVisible = false;
          this.searchName = '';
          this.getListSearchHistory();
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(error);
      } finally {
      }
    },
    /**
     * @description:读取搜索历史
     * @param {type}
     * @return:
     * @author: CH
     */
    showSearchHistory(item) {
      this.resetCondtionGroup();
      var list = JSON.parse(item.searchValue);
      if (list.conditionJson1.length > 2 || list.conditionJson2.length > 2) {
        this.isHiddenSearchLineShow = true;
        // 切换图标
        this.expandBtnIcon = 'search-bar-hide';
        this.tagName = 'text—item-show';
      }
      for (let i = 0; i < list.conditionJson1.length; i++) {
        if (list.conditionJson1[i].ptype === 'list') {
          var arr = list.conditionJson1[i].value.split(',');
          for (var s in arr) {
            arr[s] = Number(arr[s]);
          }
          list.conditionJson1[i].value = arr;
        }
        if (list.conditionJson1[i].ptype === 'boolean') {
          list.conditionJson1[i].value = Number(list.conditionJson1[i].value);
        }
        const element = this[`condition${i + 1}Group1`];
        element.column = list.conditionJson1[i].column;
        element.operator = list.conditionJson1[i].operator;
        if (i === 0) {
          this.selectCahnge1(element.column);
          element.value = list.conditionJson1[i].value;
          element.valueList = list.conditionJson1[i].value;
          element.type = list.conditionJson1[i].type;
        } else if (i === 1) {
          this.selectCahnge3(element.column);
          element.value = list.conditionJson1[i].value;
          element.valueList = list.conditionJson1[i].value;
          element.type = list.conditionJson1[i].type;
        } else if (i === 2) {
          this.selectCahnge5(element.column);
          element.value = list.conditionJson1[i].value;
          element.valueList = list.conditionJson1[i].value;
          element.type = list.conditionJson1[i].type;
        }
      }
      for (let a = 0; a < list.conditionJson2.length; a++) {
        const element = this[`condition${a + 1}Group2`];
        if (list.conditionJson2[a].ptype === 'list') {
          var arr2 = list.conditionJson2[a].value.split(',');
          for (var b in arr2) {
            arr2[b] = Number(arr2[b]);
          }
          list.conditionJson2[a].value = arr2;
        }
        if (list.conditionJson2[a].ptype === 'boolean') {
          list.conditionJson2[a].value = Number(list.conditionJson2[a].value);
        }
        element.column = list.conditionJson2[a].column;
        element.operator = list.conditionJson2[a].operator;
        if (a === 0) {
          this.selectCahnge2(element.column);
          element.value = list.conditionJson2[a].value;
          element.valueList = list.conditionJson1[a].value;
          element.type = list.conditionJson2[a].type;
        } else if (a === 1) {
          this.selectCahnge4(element.column);
          element.value = list.conditionJson2[a].value;
          element.valueList = list.conditionJson1[a].value;
          element.type = list.conditionJson2[a].type;
        } else if (a === 2) {
          this.selectCahnge6(element.column);
          element.value = list.conditionJson2[a].value;
          element.valueList = list.conditionJson1[a].value;
          element.type = list.conditionJson2[a].type;
        }
      }

      this.groupOperator = list.operatorGroup;
    }
  }
};
</script>

